.academic_home-page {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80%;
    background-color: #f8f8f8;
  }
  
  .academic_background-image {
    width: 100%;
    height: 100%;
    background-image: url('./images/bg2.jpg'); /* Update with your image path */
    background-size: cover;
    background-position: center;
    opacity: 0.9; /* Adjust the opacity as needed */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .academic_content {
    text-align: center;
    color: white;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.5); /* Background color for content */
  }
  
  h1 {
    font-size: 36px;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 18px;
  }
  
  .academic_button-container {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    gap: 10px;
    align-items: center;
  }
  
  .academic_button {
    padding: 10px 20px;
    background-color: #ff0e0e;
    border-radius: 20px;
    color: white;
    border: none;
    cursor: pointer;
    width: 150px;
  }
  


